.container {
  display: block;
}

.imgContainer {
  height: 70px;
  width: 70px;
  display: flex;
  align-items: center;
  img {
    max-width: 100%;
  }
}

.title {
  font-size: 20px;
  line-height: 26px;
  margin-top: 42px;
}

.subtitle {
  margin-top: 12px;
  color: var(--gray-1);
  font-size: 14px;
  line-height: 28px;
}

@media (max-width: 768px) and (min-width: 569px) {
  .container {
    &:nth-child(3) {
      grid-column: 1 / 3;
    }
  }
  
}
