.btn {
  position: relative;
  padding: 18px 38px;
  background: var(--bg-linear);
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1.5px;
  transition: 0.2s;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--bg-linear);
    z-index: -1;
    filter: blur(20px);
    opacity: 0;
    transition: 0.2s;
  }

  @media (min-width: 992px) {
    &:hover {
      &::before {
        opacity: 1;
      }
    }
  }
  
}

